.project-section{
    margin-top: 2rem;
    padding: 0px 20px;
    border: 1px solid rgba(0, 0, 0, 0.274);
    border-radius: 10px;
    /* box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.1); */
    background-color: white;
}
.projects{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card {
    width: 190px;
    height: 254px;
    border-radius: 20px;
    background: white;
    position: relative;
    padding: 1.8rem;
    /* border: 2px solid #c3c6ce; */
    transition: 0.5s ease-out;
    overflow: visible;
    margin-bottom: 2rem;
    margin-left: 2em;
    box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.25);

   }
   
   .card-details {
    color: black;
    height: 100%;
    grid-gap: .5em;
    gap: .5em;
    display: grid;
    place-content: center;
   }
   
   .card-button {
    -webkit-transform: translate(-50%, 125%);
            transform: translate(-50%, 125%);
    width: 50%;
    border-radius: 1rem;
    border: none;
    background-color:rgb(134, 134, 134);
    color: white;
    font-size: 1rem;
    padding: .5rem 1rem;
    position: absolute;
    left: 50%;
    bottom: 0;
    opacity: 0;
    transition: 0.3s ease-out;
   }
   
   .text-body {
    color: rgb(134, 134, 134);
   }
   
   /*Text*/
   .text-title {
    font-size: 1.5em;
    font-weight: bold;
   }
   
   /*Hover*/
   .card:hover {
    border-color: rgba(0,0,0,0.1);
    /* box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.25); */
   }
   
   .card:hover .card-button {
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    opacity: 1;
}

@media (max-width: 768px) {
    .projects{
        overflow: scroll;
        max-height: 50vh;
        justify-content: center;
    }
    .card{
        margin-left: 0px;
    }
}
